@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.App{text-align:center;position:relative;height:100vh}.app-container{margin-top:5rem}.app-modal-header-title{color:#342aa4;font-size:16px}.app-layout-container{-webkit-margin-start:18%;margin-inline-start:18%;color:#342aa4;font-size:16px}

.helppal-bar{background:linear-gradient(90deg, #342aa4 0%, #584ec8 100%, #584ec8 100%)}.helppal-bar .toolbar{display:flex;width:100%;justify-content:space-between;position:relative}.helppal-bar .toolbar .logo-text{font-size:1.6rem}.helppal-bar .toolbar .app-link{width:10rem;height:2rem}.helppal-bar .toolbar .app-link img{width:100%;height:100%;object-fit:contain}.user-actions-wrapper{display:flex;align-items:center}.app-logo-wrapper{display:flex;justify-content:center;align-items:center}

