.menu-sidebar{height:calc(100vh - 56px);width:18%;max-width:350px;z-index:999;overflow-y:auto;position:absolute;top:65px;background:#fff;box-shadow:2px 0 6px 0 rgba(0,0,0,0.2);padding-top:1rem}.menu-sidebar .menu-list{text-align:start;margin-top:1rem}.menu-sidebar .menu-list .menu-list-item{-webkit-border-start:6px solid transparent;border-inline-start:6px solid transparent;-webkit-padding-start:1rem;padding-inline-start:1rem}.menu-sidebar .menu-list .menu-list-item a{color:#333;padding:0.8rem 1rem;display:block;border-bottom:2px solid #efefef;padding-left:0}.menu-sidebar .menu-list .menu-list-item.active{-webkit-border-start:6px solid #342aa4;border-inline-start:6px solid #342aa4}.menu-sidebar .menu-list .menu-list-item:hover{background-color:#342aa433}.menu-sidebar .menu-list .menu-list-item.settings-item{margin-top:1rem}.menu-sidebar .menu-list .routes-title{padding:1rem 0 0.5rem;font-weight:800;border-bottom:2px solid #333;margin-top:1rem;-webkit-margin-start:2rem;margin-inline-start:2rem}.menu-sidebar .menu-list .menu-icon,.menu-sidebar .menu-list .map-icon,.menu-sidebar .menu-list .history-icon,.menu-sidebar .menu-list .settings-icon{color:#584ec8;margin:auto 0.5rem}.menu-sidebar .btn-helppal{width:80%}

