.profile-card-wrapper{-webkit-padding-before:1rem;padding-block-start:1rem;text-align:start}.profile-card-wrapper .profile-card-inner{border-radius:2px;background-color:#FFFFFF;box-shadow:0 1px 2px 0 rgba(0,0,0,0.3);width:38rem;min-height:23rem;padding:1.75rem}.profile-card-wrapper .profile-card-inner .profile-card-header{display:flex;justify-content:space-between}.profile-card-wrapper .profile-card-inner .profile-card-header .profile-card-img-wrapper{display:flex;justify-content:center;align-items:center}.profile-card-wrapper .profile-card-inner .profile-card-header .profile-card-img-wrapper .profile-card-img{box-sizing:border-box;height:3.25rem;width:3.25rem;border:2px solid #342AA4;box-shadow:inset 0 1px 3px 0 rgba(0,0,0,0.5);border-radius:50px}.profile-card-wrapper .profile-card-inner .profile-card-header .profile-card-img-wrapper .profile-card-img img{width:100%;height:100%;object-fit:contain}.profile-card-wrapper .profile-card-inner .profile-card-header .profile-card-img-wrapper .profile-card-img.no-image{box-shadow:none;border:none}.profile-card-wrapper .profile-card-inner .profile-card-name{color:#444444;font-size:0.875rem;font-weight:600}.profile-card-wrapper .profile-card-inner .link-edit-profile{color:#584EC8;font-size:0.875}.profile-card-wrapper .profile-card-inner h3{color:#342AA4;font-family:Roboto;font-size:1rem}.profile-card-wrapper .profile-card-inner .seprator{width:33.75rem;height:1px;background-color:#CCCCCC}.profile-card-wrapper .profile-card-inner .label{color:rgba(0,0,0,0.54);font-size:0.875rem}.profile-card-wrapper .profile-card-inner .text{font-size:0.87rem;color:#333333}.profile-card-wrapper .profile-card-inner .upload-photo-wrapper{width:10.5rem;height:2.5rem;background:#fff;padding:0.5rem 1rem;cursor:pointer;position:relative}.profile-card-wrapper .profile-card-inner .upload-photo-wrapper label{-webkit-padding-start:0.65rem;padding-inline-start:0.65rem;font-size:0.75rem;display:inline}.profile-card-wrapper .profile-card-inner .upload-photo-wrapper input[type="file"]{position:absolute;width:100%;height:100%;opacity:0;top:0;left:0}.profile-card-wrapper .profile-card-inner form label{display:inline !important;color:rgba(0,0,0,0.54);font-size:0.875rem}.profile-card-wrapper .profile-card-inner form .required-symbol{height:16px;width:7px;color:#ED1B2E;font-family:Roboto;font-size:0.875rem;font-weight:500}.profile-card-wrapper .profile-card-inner form .textbox{height:2rem;box-sizing:border-box;border:1px solid #999999;background-color:#FFFFFF;width:100%}.profile-card-wrapper .profile-card-inner form .info-icon{height:1.625rem;width:1.625rem;border:0.8px solid #444444;display:flex;justify-content:center;align-items:center;border-radius:50px}.profile-card-wrapper .profile-card-inner form .info-icon>div{color:#444444}.profile-card-wrapper .profile-card-inner form .select-language,.profile-card-wrapper .profile-card-inner form .select-year{width:100%}.profile-card-wrapper .profile-card-inner form input[type="submit"]{width:8.75rem;height:2rem;border-radius:2px;background:linear-gradient(90deg, #342AA4 0%, #584EC8 100%, #584EC8 100%);box-shadow:0 2px 3px 0 rgba(0,0,0,0.2);color:#ffff;text-transform:uppercase;text-align:center}


